import { formatToShortDateString, formatToDateString, dayjs, formatToDurationString } from '../../utils';

export { formatToShortDateString, formatToDurationString } from '../../utils';

export function getEventTemplate({ point, destination, offers }) {
  const eventType = point.type;
  const destinationName = destination.name;
  const startDateShort = formatToShortDateString(point.dateFrom);
  const startDate = formatToDateString(point.dateFrom);
  const startTimeShort = dayjs(point.dateFrom).format('HH:mm');
  const endTimeShort = dayjs(point.dateTo).format('HH:mm');
  const startTime = dayjs(point.dateFrom).format('YYYY-MM-DDTHH:mm');
  const endTime = dayjs(point.dateTo).format('YYYY-MM-DDTHH:mm');
  const eventDuration = formatToDurationString(point.dateFrom, point.dateTo);

  const eventFavoriteActiveClass = point.isFavorite ? 'event__favorite-btn--active' : '';

  const offerList = offers.map((offer) => {
    const { title, price } = offer;
    return `<li class="event__offer">
      <span class="event__offer-title">${title}</span>
        &plus;&euro;&nbsp;
        <span class="event__offer-price">${price}</span>
      </li>`;
  }).join('');

  return `
    <li class="trip-events__item">
      <div class="event">
      <time class="event__date" datetime="${startDate}">${startDateShort}</time>
      <div class="event__type">
        <img class="event__type-icon" width="42" height="42" src="img/icons/${eventType}.png" alt="Event type icon">
      </div>
      <h3 class="event__title">${destinationName}</h3>
      <div class="event__schedule">
        <p class="event__time">
          <time class="event__start-time" datetime="${startTime}">${startTimeShort}</time>
          &mdash;
          <time class="event__end-time" datetime="${endTime}">${endTimeShort}</time>
        </p>
        <p class="event__duration">${eventDuration}</p>
      </div>
      <p class="event__price">
        &euro;&nbsp;<span class="event__price-value">${point.basePrice}</span>
      </p>
      <h4 class="visually-hidden">Offers:</h4>
      <ul class="event__selected-offers">
        ${offerList}
      </ul>
      <button class="event__favorite-btn ${eventFavoriteActiveClass}" type="button">
      <span class="visually-hidden">Add to favorite</span>
      <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
        <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
      </svg>
      </button>
      <button class="event__rollup-btn" type="button">
        <span class="visually-hidden">Open event</span>
      </button>
    </div>
  </li>
  `;
}
